/* global cbLocalized */
/**
 * WordPress Dependencies
 */
import stateManager from './stateManager';
import ConditionBuilder from './components/ConditionBuilder';
import { isLegacyConditions, conditionalBlocksLogo } from './helpers';

import ButtonWithConfirm from './components/ButtonWithConfirm';
import CardWithButton from './components/CardWithButton';

const { __ } = wp.i18n;
const { addFilter } = wp.hooks;
const { Fragment, useState } = wp.element;
const { InspectorControls } = wp.blockEditor;
const { createHigherOrderComponent } = wp.compose;
const { TextControl, PanelBody, PanelRow, Button, Modal, CheckboxControl, TabPanel, ExternalLink, Spinner, Icon } = wp.components;

/**
 * Add default Attributes to all blocks.
 *
 * @param {Object} settings Settings for the block.
 *
 * @return {Object} settings Modified settings.
 */

function addAttributes(settings) {
	if (typeof settings.attributes !== 'undefined') {
		const conditionsObject = {
			conditionalBlocks: {
				type: 'object',
				default: {
					conditions: [] // array - active conditions.
				}
			}
		};

		// eslint-disable-next-line no-param-reassign
		settings.attributes = Object.assign(settings.attributes, conditionsObject);
	}

	return settings;
}

addFilter('blocks.registerBlockType', 'conditionalBlocks/attributes', addAttributes, 1);

// Save all condtions back to the block attributes.
const getConditions = () => {
	const block = wp.data.select('core/block-editor').getSelectedBlock();

	return block?.attributes?.conditionalBlocks?.conditions ? block.attributes.conditionalBlocks.conditions : [];
};

// Save all condtions back to the block attributes.
const saveConditions = (newCondtions) => {
	const block = wp.data.select('core/block-editor').getSelectedBlock();

	if (block.clientId) {
		wp.data.dispatch('core/block-editor').updateBlockAttributes(block.clientId, { conditionalBlocks: { conditions: [...newCondtions] } }, false);
	}
};

/**
 * Add mobile visibility controls on Advanced Block Panel.
 *
 * @param {Function} BlockEdit Block edit component.
 *
 * @return {Function} BlockEdit Modified block edit component.
 */
const withAdvancedControls = createHigherOrderComponent(
	(BlockEdit) => (props) => {
		const { name, attributes, isSelected, setAttributes } = props;

		// Create a copy that we can edit.
		const conditionalBlocks = attributes?.conditionalBlocks ? { ...attributes.conditionalBlocks } : [];
		// Create a copy that we can edit.
		const conditionalBlocksAttributes = attributes?.conditionalBlocksAttributes ? { ...attributes.conditionalBlocksAttributes } : false;

		// Setup our current conditions.
		const currentConditions = Array.isArray(conditionalBlocks?.conditions) ? Array.from(conditionalBlocks.conditions) : [];

		const [isOpen, setOpen] = useState(false);

		const [createPresetName, setCreatePresetName] = useState([]);

		const manager = stateManager();

		// Prevent unnecessary loading.
		if (!isOpen && !isSelected) {
			return (
				<Fragment>
					<BlockEdit {...props} />
				</Fragment>
			);
		}

		const blockHasConditions = currentConditions.length >= 1;

		const requiresConversion = !!(!blockHasConditions && isLegacyConditions(conditionalBlocksAttributes));

		const presets = cbLocalized?.isPro && manager.get('presets') ? manager.get('presets') : [];

		const openModal = () => setOpen(true);
		const closeModal = () => setOpen(false);

		const savePresets = (newPresets) => {
			if (cbLocalized?.isPro) {
				cbLocalized.presets = newPresets;
				manager.update({ presets: newPresets }, false);
			}
		};

		const convertConditions = async () => {
			if (requiresConversion) {
				const converted = await manager.convertLegacyConditions(conditionalBlocksAttributes);

				if (converted) {
					saveConditions(converted);
					// Delete the old attributes.
					setAttributes({ conditionalBlocksAttributes: false });
				}
			}
		};

		const blankPreset = (prefilledName) => ({
			id: manager.uuid4(),
			type: 'preset',
			name: prefilledName,
			conditions: []
		});

		const currentBlockTab = () => (
			<div>
				<h2>{__('Condition Builder', 'conditional-blocks')}</h2>

				<ConditionBuilder
					conditions={currentConditions}
					onChange={(updatedCondtions) => {
						saveConditions(updatedCondtions);
					}}
				/>

				{!cbLocalized?.isPro && (
					<Fragment>
						<div className="conditional-blocks-builder-modal__row">
							<CardWithButton
								content={__('Get the full experience with Conditional Blocks Pro', 'conditional-blocks')}
								buttonUrl="https://conditionalblocks.com/pricing?cb=upgrade-from-free"
								buttonText={__('Learn more', 'conditional-blocks')}
							/>
						</div>
					</Fragment>
				)}

				{manager.isDevMode && (
					<div>
						<div className="conditional-blocks-builder__spacer" />
						<div className="conditional-blocks-builder-modal__developer_section">
							<div className="conditional-blocks-builder-modal__row ">
								<h3>{__('Developer Mode', 'conditional-blocks')} 👩‍💻</h3>
								<p>
									<strong>{__("All blocks and conditions in Developer Mode are created normally. It's safe to use.", 'conditional-blocks')}</strong>
								</p>
							</div>

							<div className="conditional-blocks-builder-modal__row ">
								<h4>{__('Conditions', 'conditional-blocks')}</h4>
								<pre>{JSON.stringify(currentConditions, null, 2)}</pre>
							</div>
						</div>
					</div>
				)}
			</div>
		);

		const presetsManager = () => (
			<div>
				<h2>{__('Preset Manager', 'conditional-blocks')}</h2>
				<div className="conditional-blocks-builder-modal__row">
					<p>
						{__(
							'Presets are groups of conditions that can be applied across multiple blocks. Presets allow you to apply frequently used groups of conditions to blocks in one place, rather than having to edit each individual block on the site.',
							'conditional-blocks'
						)}
					</p>
					<p>
						{__(
							'Create and edit your presets here. Presets can then be applied to the selected block using the Condition Builder under the Current Block tab.',
							'conditional-blocks'
						)}
					</p>
					{cbLocalized?.isPro && (
						<p>
							{manager.isSaving ? (
								<Fragment>
									<Spinner style={{ display: 'inline-block' }} />
									<strong>{__('Saving changes..', 'conditional-blocks')}</strong>
								</Fragment>
							) : (
								<Fragment>
									<Icon className="conditional-blocks__save-icon" icon="cloud" />
									<strong>{__('Changes to presets save instantly to all blocks with that preset enabled.', 'conditional-blocks')}</strong>
								</Fragment>
							)}
						</p>
					)}
				</div>

				{!cbLocalized?.isPro ? (
					<Fragment>
						<CardWithButton
							content={__('Get full control with Conditional Blocks Pro', 'conditional-blocks')}
							buttonUrl="https://conditionalblocks.com/pricing?cb=upgrade-from-free"
							buttonText={__('Learn more', 'conditional-blocks')}
						/>
					</Fragment>
				) : (
					<Fragment>
						<div className="conditional-blocks-builder-modal__row">
							{Object.keys(presets).map((value, index) => {
								const preset = presets[index];

								return (
									<PanelBody className="conditional-blocks-builder-modal__single_preset" title={preset.name} key={preset.id} initialOpen={false}>
										<PanelRow>
											<ConditionBuilder
												isPreset
												conditions={Array.isArray(preset?.conditions) ? [...preset.conditions] : []}
												onChange={(updatedCondtions) => {
													const modifedPresets = [...presets];

													modifedPresets[index].conditions = [...updatedCondtions];
													savePresets(modifedPresets);
												}}
											/>
											<ButtonWithConfirm
												buttonText={__('Remove Preset', 'conditional-blocks')}
												onConfirm={() => {
													const modified = [...presets];
													// Remove this condition.
													modified.splice(index, 1);
													savePresets(modified);
												}}
											/>
										</PanelRow>
									</PanelBody>
								);
							})}
						</div>

						<div className="conditional-blocks-builder__spacer" />

						<div className="conditional-blocks-builder-modal__row">
							<h3>{__('Create Preset')}</h3>
						</div>

						<div className="conditional-blocks-builder-modal__row">
							<TextControl
								label={__('Preset Name', 'conditional-blocks')}
								help={__('Give the preset a recognizable name.', 'conditional-blocks')}
								value={createPresetName}
								onChange={(newValue) => {
									setCreatePresetName(newValue);
								}}
								placeholder={__('My Preset..', 'conditional-blocks')}
							/>
							<Button
								isSecondary
								disabled={!createPresetName.length}
								onClick={() => {
									const modifiedPresets = [...presets];
									modifiedPresets.push(blankPreset(createPresetName));
									savePresets(modifiedPresets);
									// stateManager().update({ presets: modifiedPresets });
									setCreatePresetName('');
								}}
							>
								{__('Create Preset', 'conditional-blocks')}
							</Button>
						</div>
					</Fragment>
				)}
				{manager.isDevMode && (
					<div>
						<div className="conditional-blocks-builder__spacer" />
						<div className="conditional-blocks-builder-modal__developer_section">
							<div className="conditional-blocks-builder-modal__row ">
								<h3>{__('Developer Mode', 'conditional-blocks')} 👩‍💻</h3>
								<strong>{__("All blocks and conditions in Developer Mode are created normally. It's safe to use.", 'conditional-blocks')}</strong>
							</div>

							<div className="conditional-blocks-builder-modal__row ">
								<h4>Presets</h4>
								<pre>{JSON.stringify(presets, null, 2)}</pre>
							</div>
						</div>
					</div>
				)}
			</div>
		);

		const additionalSettings = () => (
			<Fragment>
				<h2>{__('Settings')}</h2>
				<div className="conditional-blocks-builder-modal__row ">
					<h3>{__('Responsive Blocks', 'conditional-blocks')}</h3>
					<p>{__("Change the breakpoints of blocks with the Responsive (Screen sizes) condition to match your theme's breakpoints if needed.", 'conditional-blocks')}</p>
				</div>
				<div className="conditional-blocks-builder-modal__row ">
					<TextControl
						label={__('Desktop Minimum (px)', 'conditional-blocks')}
						value={manager.get('screensizes', 'device_size_desktop_min')}
						onChange={(newValue) => {
							manager.update({ screensizes: { device_size_desktop_min: newValue } });
						}}
						type="number"
						placeholder="1025"
					/>
				</div>
				<div className="conditional-blocks-builder-modal__row ">
					<TextControl
						label={__('Tablet Maximum (px)', 'conditional-blocks')}
						value={manager.get('screensizes', 'device_size_tablet_max')}
						onChange={(newValue) => {
							manager.update({ screensizes: { device_size_tablet_max: newValue } });
						}}
						placeholder="1024"
						type="number"
					/>
				</div>
				<div className="conditional-blocks-builder-modal__row ">
					<TextControl
						label={__('Tablet Minimum (px)', 'conditional-blocks')}
						value={manager.get('screensizes', 'device_size_tablet_min')}
						onChange={(newValue) => {
							manager.update({ screensizes: { device_size_tablet_min: newValue } });
						}}
						placeholder="768"
						type="number"
					/>
				</div>
				<div className="conditional-blocks-builder-modal__row ">
					<TextControl
						label={__('Mobile Maximum (px)', 'conditional-blocks')}
						value={manager.get('screensizes', 'device_size_mobile_max')}
						onChange={(newValue) => {
							manager.update({ screensizes: { device_size_mobile_max: newValue } });
						}}
						placeholder="767"
						type="number"
					/>
				</div>
				<div className="conditional-blocks-builder-modal__row ">
					<h3>{__('Developer Mode', 'conditional-blocks')}</h3>
					<p>
						{__('Activate Developer Mode manually or by using a', 'conditional-blocks')} <code>.local</code>{' '}
						{__(' domain. Developer Mode provides extra information to help development and debugging.', 'conditional-blocks')}
					</p>
					<p> {__("All blocks and conditions in Developer Mode are created normally. It's safe to use.", 'conditional-blocks')} </p>
				</div>
				<div className="conditional-blocks-builder-modal__row">
					<CheckboxControl
						label={__('Developer Mode', 'conditional-blocks')}
						checked={manager.isDevMode}
						onChange={() => {
							manager.toggleDevMode();
						}}
					/>
				</div>
				{manager.isDevMode && (
					<div>
						<div className="conditional-blocks-builder__spacer" />
						<div className="conditional-blocks-builder-modal__developer_section">
							<div className="conditional-blocks-builder-modal__row ">
								<h3>{__('Developer Mode', 'conditional-blocks')} 👩‍💻</h3>
							</div>

							<div className="conditional-blocks-builder-modal__row ">
								<h4>Settings State</h4>
								<pre>{JSON.stringify(manager.cbState, null, 2)}</pre>
							</div>
						</div>
					</div>
				)}
			</Fragment>
		);

		return (
			<Fragment>
				<BlockEdit {...props} />
				{isOpen && (
					<Modal
						icon={<Icon icon={conditionalBlocksLogo(20)} />}
						title={cbLocalized.isPro ? 'Conditional Blocks Pro' : 'Conditional Blocks'}
						onRequestClose={closeModal}
						className="conditional-blocks-builder-modal"
					>
						<div className="conditional-blocks-builder-modal__wrapper">
							<TabPanel
								className="conditional-blocks-builder-modal__tabs"
								activeClass="conditional-blocks-active-tab"
								tabs={[
									{
										name: 'tab1',
										title: `${__('Current Block', 'conditional-blocks')} - (${name})`,
										className: 'tab-one',
										content: currentBlockTab()
									},
									{
										name: 'tab2',
										title: __('Preset Manager', 'conditional-blocks'),
										className: 'tab-two',
										content: presetsManager()
									},
									{
										name: 'tab3',
										title: __('Settings', 'conditional-blocks'),
										className: 'tab-two',
										content: additionalSettings()
									}
								]}
							>
								{(tab) => <div>{tab.content}</div>}
							</TabPanel>
						</div>
					</Modal>
				)}
				{isSelected && (
					<InspectorControls>
						<PanelBody title="Visibility Conditions" icon={conditionalBlocksLogo} initialOpen={blockHasConditions || requiresConversion}>
							<PanelRow>{__('Control the visibility of this block based on conditions.')}</PanelRow>

							{blockHasConditions && (
								<Fragment>
									<h4>{__('Active Block Conditions')}</h4>
									{Object.keys(currentConditions).map((value, index) => {
										const condition = currentConditions[index];

										if (!condition.type.length) {
											return <Fragment key={condition.id} />;
										}

										return (
											<Fragment key={condition.id}>
												<div className="conditional-blocks-builder__active_condition_tag">{condition.type}</div>
											</Fragment>
										);
									})}
									<br />
									<br />
								</Fragment>
							)}
							<PanelRow>
								{(requiresConversion && (
									<Button isSecondary onClick={convertConditions}>
										{__('Convert to 2.0 conditions', 'conditional-blocks')}
									</Button>
								)) || (
									<Button isSecondary onClick={openModal}>
										{__('Configure conditions', 'conditional-blocks')}
									</Button>
								)}
							</PanelRow>
						</PanelBody>
					</InspectorControls>
				)}
			</Fragment>
		);
	},
	'withAdvancedControls'
);

addFilter('editor.BlockEdit', 'conditionalBlocks/conditionBuilder', withAdvancedControls, 10);

/**
 * Legacy Attributes.
 *
 * @param {Object} settings Settings for the block.
 *
 * @return {Object} settings Modified settings.
 */

function legacyAttributes(settings) {
	// check if object exists for old Gutenberg version compatibility

	if (typeof settings.attributes !== 'undefined') {
		const conditionsObject = {
			conditionalBlocksAttributes: {
				type: 'object',
				default: {
					hideOnMobile: false, // Boolean.
					hideOnTablet: false, // Boolean.
					hideOnDesktop: false, // Boolean.
					userState: '',
					userRoles: [], // array.
					httpReferer: '', // String.
					httpUserAgent: [], // array.
					postMeta: {
						key: '',
						value: '',
						operator: 'true'
					},
					dates: [] // Array.
				}
			}
		};

		settings.attributes = Object.assign(settings.attributes, conditionsObject);
	}

	return settings;
}

addFilter('blocks.registerBlockType', 'conditionalBlocks/custom/attributes', legacyAttributes, 1);

/**
 * Add a class to blocks that have condtions.
 */
const withCustomClassName = createHigherOrderComponent(
	(BlockListBlock) => (props) => {
		const { attributes } = props;

		const { conditionalBlocks, conditionalBlocksAttributes } = attributes;
		// Setup our current conditions.
		const currentConditions = Array.isArray(conditionalBlocks?.conditions) ? conditionalBlocks.conditions : [];

		const blockHasConditions = currentConditions.length >= 1;

		const isLegacy = isLegacyConditions(conditionalBlocksAttributes);

		if (blockHasConditions || isLegacy) {
			return <BlockListBlock {...props} className="conditional-blocks-has-conditions" />;
		}
		return <BlockListBlock {...props} />;
	},
	'withClientIdClassName'
);

addFilter('editor.BlockListBlock', 'conditionalBlocks/editor/blockClasses', withCustomClassName);
